// [/]    
export const formattedCurrencyCodes = Object.entries(currencyCodes).map(([name, code]) => `${name}/${code}`);

export const chartOptions = {
    chart: {
      id: 'currency-chart',
      type: 'area',
      height: 350,
      zoom: {
        autoScaleYaxis: true
      }
    },
    xaxis: {
      type: 'datetime',
    },
    dataLabels: {
      enabled: false
    },
    markers: {
      size: 0,
      style: 'hollow',
    },
    tooltip: {
      x: {
        format: 'yy.MM.dd'
      }
    },
    stroke: {
      curve: 'smooth'
    },
    fill: {
      type: 'gradient',
      gradient: {
        shadeIntensity: 1,
        inverseColors: false,
        opacityFrom: 0.5,
        opacityTo: 0,
        stops: [0, 90, 100]
      },
    }
  };

export function markerChange(chartOptions,series){

  const lastIndex = series[0].data.length - 1;

  const markers= {
    discrete: [{
      seriesIndex: 0,
      dataPointIndex: lastIndex, //    
      fillColor: '#ff4560', //    
      strokeColor: '#fff',
      size: 8 //     
    }],
  }
  
  // dataLabels 
  const dataLabels = {
    enabled: true,
    enabledOnSeries: [0], //    
    formatter: function (val, opts) {
      //     
      if (opts.dataPointIndex === lastIndex) {
        return val.toFixed(2); //    
      } else {
        return '';
      }
    },
    background: {
      enabled: true,
      borderRadius: 2,
      padding: 4,
      opacity: 0.9,
      borderWidth: 1,
      borderColor: '#FF4560',
      dropShadow: {
        enabled: true,
        top: 1,
        left: 1,
        blur: 1,
        opacity: 0.45,
      },
    },
    style: {
      fontSize: '12px',
      fontFamily: 'Helvetica, Arial, sans-serif',
      colors: ['#FF4560'],
    },
    offsetY: -10,
  };



  return { ...chartOptions, markers, dataLabels };
}



